  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}
.breadcrumbs{
  margin-left: 0 !important;
  color: white;
  width: 800px;
}
.breadcrumbs-item-text {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}

