    text-align: center
}
.cursor-pointer{
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true{
  background-color: #fbb93e;
}
.background-enable-false{
  background-color: #666666;
}
.breadcrumbs{
  margin-left: 0 !important;
  color: white;
  width: 800px;
}
.breadcrumbs-item-text {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.v-breadcrumbs__item {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: inline;
  text-decoration: none;
  -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}

